.contato {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2em;
}
.dados {
  padding: 0px;
  list-style: none;
}
.dados li {
  font-size: 1.2em;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 0.8em;
}
.dados li:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 2px;
  background: #ddd;
  margin-right: 10px;
}
